
.container{
    display: grid;
    grid-template-columns: 60px auto;
    height: 100vh;
    margin: 0;
    padding: 0;
}


.artistacontainer{
    width: auto;
}

.datos{
    margin: 30px;
    width: 90%;
}

.datos h1 {
    margin-bottom: 5px;
}

.datos h3 {
    margin-bottom: 15px;
}

/****************CAROUSEL***********/

.carousel{
    z-index: 1;
    position: relative;
    margin: 50px 10% 0px 5%;
    cursor: pointer;
}

.elementofcarousel img{
    height: 60vh;
    object-fit: scale-down;
}


/***************************VER MAS*/

.componentevermas {
    text-align: justify;
    width: auto;
}

.componentevermasancho {
    text-decoration: none;
    color:rgb(189, 22, 22);
}

.componentevermasancho a:hover { text-decoration: none; color:rgb(189, 22, 22); }
.componentevermasancho a:focus { text-decoration: none; color:rgb(189, 22, 22); }
.componentevermasancho a:active { text-decoration: none; color:rgb(189, 22, 22); }


/************************************/

/*****INVESTIGADOR*************/

.textos{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    margin: 25px;
}

.textos #textotitulo{
    margin-top: 25px;
    font-size: 32px;
    color:rgb(189, 22, 22);
    font-weight: bolder;
}

.textos #textocuerpo{
    margin-top: 15px;
    font-size: 20px;
}

/************************************/

@media (max-width: 750px){
    .container{
        grid-template-columns: 100%;
        grid-template-rows: 60px auto;
    }

    .carousel{
        margin: 50px;
    }

    .datos{
        margin: 10px 30px 0px 30px;
        width: 85%;
    }
}